.popup {
    border: 0px;
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    top: 50%;
    left: 50%;
    /* transform: translate(-50%, -50%); */
    top: 0;
    left: 0;
    z-index: 20;
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup:focus {
    outline: none;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75)
}

.backgroundPic {
    position: absolute;
    border: 0px;
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 0;
    width: 600px;
    height: 800px;
    z-index: -10;
}

.content {
    width: 500px;
    height: 600px;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.content > div {
    font-size: x-large;
}

.buttonsection {
    width: 100%;
    margin-top: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.buttonsection > div {
    color: darkblue;
    margin-top: auto;
}

.buttonsection > div:hover {
    color: blue;
    cursor: pointer;
}
